"use client"

import { useState } from "react"
import { useAuth } from "@/context/auth-context"
import ItemForm from "./item-form"
import ItemCard from "./item-card"

export default function ItemsGrid() {
  const { items, deleteItem } = useAuth()
  const [showForm, setShowForm] = useState(false)
  const [filter, setFilter] = useState("semua")

  const filteredItems = filter === "semua" ? items : items.filter((item) => item.status === filter.replace("_", ""))

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-foreground mb-2">Item Terdaftar</h1>
          <p className="text-muted-foreground">Total {items.length} item dalam sistem</p>
        </div>
        <button
          onClick={() => setShowForm(!showForm)}
          className="bg-primary text-primary-foreground px-6 py-3 rounded-lg hover:bg-primary/90 transition-colors font-semibold"
        >
          {showForm ? "Tutup Form" : "Tambah Item"}
        </button>
      </div>

      {showForm && <ItemForm onClose={() => setShowForm(false)} />}

      <div className="flex gap-3 flex-wrap">
        {[
          { label: "Semua", value: "semua" },
          { label: "Aktif", value: "aktif" },
          { label: "Tidak Aktif", value: "tidak_aktif" },
        ].map((btn) => (
          <button
            key={btn.value}
            onClick={() => setFilter(btn.value)}
            className={`px-4 py-2 rounded-lg transition-colors ${
              filter === btn.value
                ? "bg-primary text-primary-foreground"
                : "bg-white text-foreground border border-border hover:bg-muted"
            }`}
          >
            {btn.label}
          </button>
        ))}
      </div>

      {filteredItems.length === 0 ? (
        <div className="text-center py-12 bg-white rounded-xl border border-border">
          <p className="text-muted-foreground">Tidak ada item ditemukan</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredItems.map((item) => (
            <ItemCard key={item.id} item={item} onDelete={() => deleteItem(item.id)} />
          ))}
        </div>
      )}
    </div>
  )
}
